@import '../variables';

.pizza-block {
  width: 280px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 172px;
  &__image {
    width: 260px;
  }

  &__title {
    font-size: 20px;
    font-weight: 900;
    letter-spacing: 1%;
    margin-bottom: 20px;
  }

  &__selector {
    display: flex;
    background-color: #f3f3f3;
    border-radius: 10px;
    flex-direction: column;
    padding: 6px;
    width: 100%;
    ul {
      display: flex;
      flex: 1;

      &:first-of-type {
        margin-bottom: 6px;
      }

      li {
        padding: 8px;
        flex: 1;
        cursor: pointer;
        font-weight: 600;
        font-size: 14px;
        @include noselect();
        &.active {
          background: #ffffff;
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
          border-radius: 5px;
          cursor: auto;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    width: 100%;
  }

  &__price {
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.015em;
  }
}
.skeleton{
  &_title{
    width: 280px;
    height: 30px;
  }
  &__main{
    width: 280px;
    height: 70px;
  }
  &_price{
    width: 100px;
    height: 35px;
  }
  &_add{
    width: 140px;
    height: 35px;
  }
}
@media (max-width: 768px) {
  .skeleton{
    &_title{
      width: 210px;
      height: 20px;
      y: 0;
    }
    &__main{
      width: 210px;
      height: 70px;
      y: 40;
    }
    &_price{
      width: 70px;
      height: 35px;
      y: 135;
    }
    &_add{
      width: 100px;
      height: 35px;
      x: 110;
      y: 135;
    }
  }
  .pizza-block {
    width: 210px;
    &__image {
      width: 260px;
    }

    &__title {
      font-size: 16px;
      margin-bottom: 10px;
    }
    &__selector {
      padding: 4px;
      ul {
        li {
          padding: 8px;
          font-size: 14px;
        }
      }
    }
    &__bottom {
      margin-top: 10px;
    }
  
    &__price {
      font-size: 16px;
      line-height: 20px;
    }
  }
}
